@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    outline: none;
    border: none;
}

:root {
    --bg-color: #F6F7FB;
    --main-color: #3B50DF;
    --texts-color: #B2BAC6;
    --manu-bar-color: #222E3C;
    --reserve-color: #1c2333;
    --main-font: "Inter", sans-serif;
    --reserve-text-color: #fff;
}

body {
    background: var(--bg-color);
}

.app {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-between;
}

.menubar {
    width: 315px;
    height: 100vh;
    padding: 20px 0 15px;
    transform: translateX(-100%);
    background-color: var(--manu-bar-color);
    display: flex;
    flex-direction: column;
    gap: 20px;
    position: fixed;
    top: 0;
    transition: 0.3s;
    z-index: 1000;
    left: 0;

    h1 {
        color: var(--reserve-text-color);
        font-family: var(--main-font);
        padding: 10px 25px;
        font-size: 25px;
    }
}

.links {
    display: flex;
    flex-direction: column;
    padding: 5px 0;

    a {
        display: flex;
        align-items: center;
        gap: 10px;
        padding: 20px 40px;
        color: var(--texts-color);
        text-decoration: none;
        font-size: 19px;
        font-family: var(--main-font);
        transition: 0.3s;

        svg {
            width: 23px;
            height: 23px;
            transition: 0.3s;
        }

        &:hover {
            color: var(--reserve-text-color);
            background-color: var(--reserve-color);
            svg {
                color: var(--main-color);
            }
        }
    }

    .active{
        color: var(--reserve-text-color);
        background-color: var(--reserve-color);
        svg {
            color: var(--main-color);
        }
    }
}

.menubar-act {
    transform: translateX(0%);
}

.main-page {
    width: 100vw;
    height: auto;
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    transition: 0.3s;
}

.not{
    width: 77vw;
    height: 89vh;
    transition: 0.3s;

    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.not-act{
    width: 100%;
}

.table{
    width: 77vw;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.3s;

    h1{
        font-family: var(--main-font);
    }
}

.table-act{
    width: 100%;
}

@import "Main.scss";
@import "Nav.scss";
@import "Settings.scss";

@media (max-width: 768px) {
    .app {
        flex-direction: column;
    }

    .menubar {
        width: 250px;
        transform: translateX(-100%);
    }

    .menubar-act {
        transform: translateX(0);
    }

    .main-page {
        width: 100%;
    }

    nav {
        width: 100%;
        padding: 0 15px;
    }

    .nav {
        width: 100%;
        justify-content: flex-end;
    }

    .menu-toggle-icon {
        display: block;
    }

    .menubar .menu-toggle-icon {
        display: none;
    }

    .menubar-act .menu-toggle-icon {
        display: block;
    }

    .set {
        width: 100%;
    }

    .set-act .cards {
        width: 100%;
    }
}

@media (max-width: 300px) {
    .menubar{
        width: 250px;
    }

    nav{
        padding: 0 10px;
    }
}
