.set {
    width: 77%;
    height: auto;
    display: flex;
    justify-content: center;
    transition: 0.3s;
}

.set-act {
    width: 100%;
    .cards {
        width: 77%;
    }
}

.cards {
    width: 100%;
    gap: 25px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 50px 0;
    transition: 0.3s;

    .card {
        width: 280px;
        height: 80px;
        background-color: var(--reserve-text-color);
        display: flex;
        align-items: center;
        border-radius: 5px;
        box-shadow: 0 0 15px rgba(0, 0, 0, 0.247);
        padding: 15px;
        gap: 15px;
        font-family: var(--main-font);
        position: relative;

        .img {
            width: 50px;
            height: 50px;
            background-color: #D3E2F7;
            border-radius: 100%;
            display: flex;
            justify-content: center;
            align-items: center;

            svg {
                width: 25px;
                height: 25px;
                color: var(--main-color);
            }
        }

        .tex {
            display: flex;
            flex-direction: column;
            gap: 10px;

            span {
                font-weight: 700;
                font-size: 20px;
                color: #929AA0;
            }
            p {
                color: #929AA0;
                font-weight: 400;
            }
        }
    }
}